<template>
  <div class="slider"
    v-finger:singleTap="closeGallery">
    <div class="item-wrapper"
      v-transform
      v-finger:pressMove="handlePressMove"
      v-finger:touchStart="handleTouchStart"
      v-finger:touchEnd="handleTouchEnd"
      v-finger:swipe="handleSwipe">
      <!-- index:keyindex -->
      <VuerSingle class="item"
        v-for="(src,index) in imgList"
        :key="src + index"
        ref="img"
        :src="src"
        :class="{z1:currentIndex===index}"
        @disableSwipe="allowSwipe = false"
        @enableSwipe="allowSwipe = true" />
    </div>
  </div>
</template>

<script>
import VuerSingle from './VuerSingle'
import To from './to.js'
export default {
  components: { VuerSingle },
  data() {
    return {
      imgList: [],
      isSingle: false,
      isShow: false,
      allowSwipe: false,
      currentIndex: 0,
      /* swipeswipe
       * handleTouchEnd100swipe
       * 100handleSwipe
       * currentIndex
       */
      swipeDelta: 0,
      swipeThreshold: 100
    }
  },
  beforeRouteLeave(to, from, next) {
    console.log('beforeRouteLeave')
    // 
    vm.isShow = false
    next()
  },
  computed: {
    maxIndex() {
      return this.imgList.length - 1
    }
  },
  watch: {
    isShow(val) {
      if (val) {
        //  history 
        if (/android/i.test(navigator.userAgent)) {
          history.pushState(null, null, location.href)
          window.addEventListener(
            'popstate',
            e => {
              this.closeGallery()
            },
            {
              once: true
            }
          )
        }
        document.querySelector('.slider').className = 'slider open'
      } else {
        document.querySelector('.slider').className = 'slider close'
      }
    },
    currentIndex() {
      //  
      let el = document.querySelector('.item-wrapper')
      el.translateX = -this.currentIndex * el.getBoundingClientRect().width
    },
    imgList() {
      let el = document.querySelector('.item-wrapper')
      el.translateX = -this.currentIndex * el.getBoundingClientRect().width
    }
  },
  methods: {
    closeGallery() {
      this.isShow = false
      this.$refs.img[this.currentIndex].reset()
    },
    handlePressMove(e, el) {
      e.preventDefault()
      if (this.allowSwipe === false || this.isSingle) return
      el.translateX += e.deltaX
      this.swipeDelta += e.deltaX
    },
    handleTouchStart() {
      To.stopAll()
    },
    handleTouchEnd(e, el) {
      // touchmoveswipe
      if (Math.abs(this.swipeDelta) < this.swipeThreshold) this.swipeDelta = 0
      let width = el.getBoundingClientRect().width
      new To(el, 'translateX', -this.currentIndex * width, 200, this.ease)
    },
    handleSwipe(evt, el) {
      // swipeDeltaswipeThreshold
      if (Math.abs(this.swipeDelta) < this.swipeThreshold) {
        this.swipeDelta = 0
        return
      }
      let width = el.getBoundingClientRect().width
      if (evt.direction === 'Left' && this.currentIndex < this.maxIndex) {
        this.$refs.img[this.currentIndex].reset()
        this.currentIndex += 1
      } else if (evt.direction === 'Right' && this.currentIndex > 0) {
        this.$refs.img[this.currentIndex].reset()
        this.currentIndex -= 1
      }
      new To(el, 'translateX', -this.currentIndex * width, 200, this.ease)
      this.swipeDelta = 0
    }
  }
}
</script>

<style scoped>
.slider {
  position: fixed;
  top: 100%;
  width: 100%;
  left: 0;
  height: 100%;
  background: #000;
  overflow: hidden;
  z-index: 1000;
  opacity: 0;
  -webkit-touch-callout: none;
  user-select: none;
}

.item-wrapper {
  display: flex;
  justify-content: flex-start;
  height: 100%;
  /* wrapper 100% */
}

.item {
  height: 100%;
  flex-basis: 100%;
  flex-shrink: 0;
}

/* swipe */
.z1 {
  z-index: 1;
}

.open {
  animation: open 0.3s;
  animation-fill-mode: forwards;
}

@keyframes open {
  0% {
    opacity: 0;
    transform: translateY(0);
  }
  100% {
    opacity: 1;
    transform: translateY(-100%);
  }
}

.close {
  animation: close 0.3s;
  animation-fill-mode: forwards;
}

@keyframes close {
  0% {
    opacity: 1;
    transform: translateY(-100%);
  }
  100% {
    opacity: 0;
    transform: translateY(0);
  }
}
</style>



